      <div class="preview__wave"></div>
      <router-link class="common-button" :to="{name: 'shipmentsearch'}" tag="a">Считаем!</router-link>
    </div>
    <div class="bottom-row container d-flex justify-content-between align-items-center">
        <div class="home-page__copy-right">
          2018 Loggie
        </div>
      <div class="home-page__social">
        <a href="#" class="fb social-icon"></a>
        <a href="#" class="twitter social-icon"></a>
        <a href="#" class="vk social-icon"></a>
      </div>
    </div>
    <div class="home-page__waves"></div>
  </div>
  </div>
</template>

<script>
  import '../assets/Homepage.css';
  import MainMenu from './MainMenu';
  export default {
    name: 'homepage',
    components: {
      MainMenu
    }
  }
</script>



