<template>
<ModalTemplate v-bind:from="from">
    <h5 slot="modal-title" class="modal-title">Вход в аккаунт</h5>
    <form slot="form">
        <div class="controls-container">
            <CombinedInput type="text" labelText="Логин" name="login" placeholder="Wadduphey" />
            <CombinedInput type="text" labelText="Пароль" name="password" placeholder="•••••••••••••••••" />
        </div>
        <div class="modal-controls">
            <label class="checkbox">
                <input type="checkbox" name="remember" />
                <span class="checkbox__checker" />
                <span class="text">Запомнить</span>
            </label>
            <router-link class="recovery" :to="{name: 'modalrecovery'}" tag="a">Восстановление пароля</router-link> 
        </div>
        <div class="buttons-container">
            <span class="common-button submit">Создать</span>
            <router-link class="common-button-white submit" :to="{name: 'modalregistration'}" tag="button">Регистрация</router-link> 
        </div>
    </form>
</ModalTemplate>
</template>

<script>
import ModalTemplate from './ModalTemplate';
import CombinedInput from './CombinedInput';

export default {
    name: 'modallogin',
    data() {
        return {
            from : ''
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.$data.from = from.name === null || ['modalrecovery', 'modalregistration'].includes(from.name) ? 'homepage' : from.name;
        });
    },
    components: {
        ModalTemplate,
        CombinedInput
    }
}
</script>


