          <router-link :to="{name: 'modallogin'}" tag="a" class="menu__actions menu__sing-in">Вход</router-link>
          <router-link :to="{name: 'modalregistration'}" tag="a" class="menu__actions button button--wapasha button--round-s d-flex align-items-center justify-content-center">Регистрация</router-link>
         <div class="menu__change-lang menu__actions">
            <div class="select">
              <input id="rus" class="d-none" type="radio" name="action" value="rus" checked>
              <label for="rus" class="rus lang-logo"></label>
              <input id="en" class="d-none" type="radio" name="action" value="en">
              <label for="en" class="en lang-logo"></label>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import '../assets/Menu.css';

export default {
  name: 'mainmenu',
};
</script>



