<template>
<ModalTemplate v-bind:from="from">
    <h5 slot="modal-title" class="modal-title">Восстановление пароля</h5>
    <div slot="modal-add-description" class="modal-add-description">
        Забыли пароль? Напишите Ваш Email и мы вышлем инструкцию по восстановлению на указанный адрес
    </div>
    <form slot="form">
        <div class="controls-container">
            <CombinedInput type="text" labelText="Email" name="email" placeholder="VasyaPupkin@gmail.com" />
        </div>
        <div class="back">
            <router-link :to="{name: 'modallogin'}" tag="a">Вернуться ко входу в аккаунт</router-link> 
        </div>
        <span class="common-button submit">Восстановить</span>
    </form>
</ModalTemplate>
</template>

<script>
import ModalTemplate from './ModalTemplate';
import CombinedInput from './CombinedInput';

export default {
    name: 'modalrecovery',
    data() {
        return {
            from : ''
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.$data.from = from.name === null || ['modallogin', 'modalregistration'].includes(from.name) ? 'homepage' : from.name;
        });
    },
    components: {
        ModalTemplate,
        CombinedInput
    }
}
</script>


