<template>
<div class="modal">
    <div class="modal-content">
        <div class="modal-container">
            <div class="modal-header">
                <slot name="modal-title"></slot>
            </div>
            <div class="modal-body">
                <slot name="modal-add-description"></slot>
                <div class="modal-form">
                    <slot name="form"></slot>
                </div>
            </div>
        </div>
    </div>
    <router-link class="modal-close" :to="{name: from}" tag="div">
        <div class="text">Закрыть</div>
        <div class="modal-button">
            <span class="cross first" />
            <span class="cross second" />
        </div>
    </router-link>
</div>
</template>

<script>
import '../assets/Modal.css';

export default {
    name: 'modal',
    props: {
        from: String
    }
}
</script>


