import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage';
import ModalRecovery from '@/components/ModalRecovery';
import ModalRegistration from '@/components/ModalRegistration';
import ModalLogin from '@/components/ModalLogin';
import Shipmentresult from '@/components/Shipmentresult';
import ShipmentSearch from '@/components/ShipmentSearch';

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      name: 'homepage',
      path: '/',
      component: Homepage
    },
    {
      name: 'modalregistration',
      path: '/registration',
      component: ModalRegistration
    },
    {
      name: 'modalrecovery',
      path: '/recovery',
      component: ModalRecovery
    },
    {
      name: 'modallogin',
      path: '/login',
      component: ModalLogin
    },
    {
      name: 'shipmentresult',
      path: '/shipmentresult',
      component: Shipmentresult
    },
    {
      name: 'shipmentsearch',
      path: '/shipmentsearch',
      component: ShipmentSearch
    },
    {
      name: 'carriers',
      path: '/',
      component: Homepage
    },
    {
      name: 'clients',
      path: '/',
      component: Homepage
    },
    {
      name: 'premium',
      path: '/',
      component: Homepage
    }
  ],
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 };
  }
})



