<template>
<ModalTemplate v-bind:from="from">
    <h5 slot="modal-title" class="modal-title">Новый аккаунт</h5>
    <form slot="form">
        <div class="controls-container">
            <CombinedInput type="text" labelText="Логин" name="login" placeholder="Wadduphey" />
            <CombinedInput type="text" labelText="Email" name="email" placeholder="VasyaPupkin@gmail.com" />
            <CombinedInput type="text" labelText="Пароль" name="password" placeholder="•••••••••••••••••" />
        </div>
        <p class="modal-terms">* Нажимая «Создать» Вы соглашаетесь с условиями пользовательского соглашения</p>
        <span class="common-button submit">Создать</span>
    </form>
</ModalTemplate>
</template>

<script>
import ModalTemplate from './ModalTemplate';
import CombinedInput from './CombinedInput';

export default {
    name: 'modalregistration',
    data() {
        return {
            from : ''
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {
            vm.$data.from = from.name === null || ['modalrecovery', 'modallogin'].includes(from.name) ? 'homepage' : from.name;
        });
    },
    components: {
        ModalTemplate,
        CombinedInput
    }
}
</script>


