<template>
  <div id="app">
      <transition name="fade" mode="out-in">
        <router-view/>
      </transition>
  </div>
</template>

<script>
import './assets/bootstrap-reboot.css';
import './assets/bootstrap-grid.css';
import './assets/Fonts.css';
import './assets/buttons.css';

import MainMenu from './components/MainMenu';

export default {
  name: 'App',
  components: {
    MainMenu,
  },
};
</script>

<style>
* {
  box-sizing: border-box;
}

a {
  font-weight: inherit;
  font-size: inherit;
  color: inherit;
  text-decoration: none;
}

#app {
  font-family: 'Circe', 'Roboto', 'Arial', sans-serif;
  min-height: 100vh;
  padding: 20px;
  display: flex;
  align-items: stretch;
  background-color: #f7f7f7;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
.fade-enter-active,
.fade-leave-active {
  animation: fadeIn 0.35s forwards;
}

.fade-leave-active {
  animation: fadeIn 0.35s reverse;
}

.backgorund {
  width: 100%;
  background-color: #fff;
  display: flex;
  flex-flow: row wrap;
}

.backgorund > div {
  width: 100%;
}

.common-button,
.common-button-white {
  background-color: #fff;
  padding: 20px 51px;
  border-radius: 32px;
  outline: 0;
  border: 0;
  cursor: pointer;
  font-size: 16px;
  font-weight: bold;
  position: relative;
  z-index: 1;
  display: inline-block;
}

.common-button:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  background: #425fff;
  z-index: -1;
  opacity: 0;
  transform: scale3d(0.6, 0.6, 1);
  transition: transform 0.3s, opacity 0.3s;
  transition-timing-function: cubic-bezier(0.75, 0, 0.125, 1);
}

.common-button {
  background-image: linear-gradient(351deg, #274bff, #081cff);
  color: #fff;
  box-shadow: 0px 14px 21px 0 rgba(0, 15, 255, 0.15);
  transition: all 0.3s ease-in-out;
}

.common-button:hover:before  {
  transform: scale3d(1, 1, 1);
  opacity: 1;
}

.common-button-white {
  background-image: white;
  color: rgba(0, 0, 0, 0.2);
  border: solid 1px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease-in-out;
}

.common-button-white:hover {
  background-image: linear-gradient(351deg, #274bff, #081cff);
  color: #fff;
  box-shadow: 0px 14px 21px 0 rgba(0, 15, 255, 0.15);
}

.common-button:hover {
  color: #fff;
  text-decoration: none;
}

.sub-title {
  font-size: 20px;
  font-weight: 300;
  letter-spacing: 0.5px;
  color: #6e6e6e;
}

.title {
  font-size: 65px;
  font-weight: 300;
  letter-spacing: 0.7px;
  line-height: 60px;
  margin-bottom: 10px;
}
</style>



