<template>
  <div class="backgorund">
    <MainMenu/>
  <div id="shipmentsearch">
    <div class="shipment-search container d-flex align-items-center justify-content-between flex-column">
    <h1 class="title">Считаем</h1>
    <p class="sub-title">Это очень просто — всего лишь заполните поля ниже</p>
    <form slot="form" class="shipment-search__form d-flex flex-column align-items-center justify-content-center">
      <div class="controls-container">
        <CombinedInput type="text" labelText="Город: Откуда" name="cityFrom" placeholder="Шанхай, Китай" />
        <CombinedInput type="text" labelText="Город: Куда" name="cityTo" placeholder="Владивосток, Россия" />
        <CombinedInput type="text" labelText="Контейнер" name="container" placeholder="40ft" />
        <CombinedInput type="text" labelText="Условия Поставки" name="conditions" placeholder="CIF" />
        <CombinedInput type="text" labelText="Прибытие" name="arrival" placeholder="До склада" />
      </div>
      <router-link :to="{name: 'shipmentresult'}" tag="a" class="common-button shipment-result__link">Посчитать</router-link>
      <!--<input type="submit" class="common-button submit" value="Посчитать" />-->
    </form>
    </div>
  </div>
  </div>
</template>

<script>
  import MainMenu from './MainMenu';
  import CombinedInput from './CombinedInput';
  import ShipmentSearch from '../assets/ShipmentSearch.css';
  export default {
    name: 'shipmentsearch',
    data() {
      return {
        from : ''
      }
    },
    components: {
      MainMenu,
      CombinedInput
    }
  }
</script>



